if (window.location.href.includes("blog")) {
    const posts = [
        {
            title: "Decoupling ahead? Bitcoin and Ethereum may finally snap their 36-month correlation",
            cardDesc: "",
            img: "https://images.cointelegraph.com/images/1434_aHR0cHM6Ly9zMy5jb2ludGVsZWdyYXBoLmNvbS91cGxvYWRzLzIwMjEtMDcvMzQ0NDZmYTEtYWIyMC00ZDhjLWE1YmUtMGE2NzJkNGI3ZWNjLmpwZw==.jpg",
            subtitle: "Bitcoin and Ethereum have been majorly trading in sync since 2018, increasing risk exposure of crypto-only investment portfolios.",
            content: `Anish Saxena, a New Delhi-based automobile dealer, made “incredible” profits by investing in cryptocurrencies in 2020, just as his business took a hit from the coronavirus pandemic-induced lockdown.

            “I had known about Bitcoin and Ethereum and dozens of other assets for years,” the 33-year old businessman said. “But I only got to invest in them after the lockdown pushed me and my family members out of work. And it helped us survive — big time.”<br/><br/>
            
            Saxena revealed that he had allocated about 80% of his investment portfolio to Bitcoin (BTC) and Ether (ETH), with the rest of his capital distributed across Polygon, Dogecoin (DOGE) and Chainlink’s LINK. His crypto-only investment netted him great profits, the numbers of which Saxena declined to reveal. <br/><br/>
            
            However, he did notice how he almost got half of his unrealized profits wiped by deciding not to liquidate ahead of the May 2021 crash.<br/><br/>
            
            “I was liquidating cryptocurrencies based on my household demand for cash,” Saxena said. “While I am still in profits, seeing my profits decline by more than 50% has prompted me to get a huge portion of my investments back into cash.”<br/><br/>
            
            Correlation risks
            Retail traders like Saxena have come under pressure due to over-reliance on the two most predominant cryptocurrencies: Bitcoin and Ether.<br/><br/>
            
            While different in terms of economics and use cases, both digital assets tend to move in the same direction. In recent history, their losses and profits appeared well-synced, illustrating that their holders might see their investments grow rapidly during bull trends but, at the same time, risk losing a lot when the uptrend exhausts and reverses to the bearish side.`,
            tag: "news",
            author: "Alex",
            authorImg: "/assets/img/team/alex.png",
            time: "22/07/2021",
            hashtags: ["crypto", "bitcoin", "ethereum"]
        }
    ]

    const urlParams = new URLSearchParams(window.location.search);
    const postId = urlParams.get('id');

    if (postId) {

        const post = posts[postId];
        const postEl = document.getElementById("post");
        const postWrapperEl = document.createElement("div");
        postEl.appendChild(postWrapperEl);
        postWrapperEl.classList.add("col-12", "col-md-8");

        postWrapperEl.innerHTML += `
                <div class="post-info d-flex justify-content-between mb-3">
                    <div class="post-author">
                        <img class="post-author-img" src="` + post.authorImg + `"/>
                        <strong>` + post.author + `</strong>
                    </div>
                    <div class="post-time">` + post.time + `</div>
                </div>
                <div class="post-img-wrapper mb-4">
                    <img class="post-img" src="` + post.img + `"/>
                </div>
                <div class="post-info mt-3 d-flex flex-column justify-content-between">
                    <h6 class="post-title fw-bold fs-4 display-3 lh-sm mb-4">` + post.title + `</h6>
                    <h5 class="post-subtitle mb-4">` + post.subtitle + `</h5>
                    <div class="post-content">` + post.content + `</div>
                </div>`

        const latestPostsWrapper = document.createElement("div");
        latestPostsWrapper.classList.add("col-12", "col-md-4");
        const latestPosts = posts.slice(1).slice(-5).reverse();
        alert(latestPosts)

        latestPosts.forEach(
            (p, index) => {
                let title;
                if (index === 0) {
                    title = `<h5 class="latest-post-title">` + p.title + `</h5>`
                } else {
                    title = `<h5 class="post-title">` + p.title + `</h5>`
                }

                if (latestPosts.length > index + 1) {
                    title.add(`<hr>`)
                }

                latestPostsWrapper.appendChild(title)
            }
        )

        postEl.appendChild(latestPostsWrapper);

    } else {
        const postList = document.getElementById("post-list");

        posts.forEach((post, index) => {
            const cardWrapper = document.createElement("div");
            cardWrapper.classList.add("post-card-wrapper", "col-12", "col-md-6", "col-lg-3", "mb-4");

            const card = document.createElement("div");
            card.classList.add("post-card");

            card.innerHTML += `
            <a href="blog.html?id=` + index + `">
                <div class="post-card-img-wrapper">
                    <img class="post-card-img" src="` + post.img + `"/>
                    <div class="post-card-subtitle">` + post.subtitle + `</div>
                </div>
                <div class="post-card-info d-flex flex-column justify-content-between">
                    <div class="post-card-title fw-semi-bold">` + post.title + `</div>
                    <div class="d-flex justify-content-between fw-normal">
                        <div class="post-card-author">by <strong>` + post.author + `</strong></div>
                        <div class="post-card-time">` + post.time + `</div>
                    </div>
                </div>
            </a>`;

            cardWrapper.appendChild(card)
            postList.appendChild(cardWrapper);

        });
    }

}
